<template>
  <div>
    <div>
      <button class="new-strategy"><i>+</i>新建策略</button>
      <span class="button-right">
        <button>下载已保存策略</button>
        <button>回复所有</button>
        <button>测试白名单</button>
        <button>上传策略</button>
        <button class="btn-delete">
        <i class="iconfont icon-delete">
          &#xe663;
        </i>
      </button>
      </span>
    </div>
    <section class="table-wrapper" >
      <strategy-table></strategy-table>
    </section>
  </div>
</template>
<script type="text/ecmascript-6">
  import strategyTable from '../components/strategyTable'

  export default {
    name: 'strategy',
    components: {
      strategyTable
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  .table-wrapper
    width: 100%
  //
  button
    line-height :30px
    padding :0  18px
    margin: 42px 10px 30px 0px
    font-size :1.2em
    color :#FFFFFF
    border:#3E7AF6 solid 1px
    background :#3E7AF6
  .new-strategy
    i
      font-size :20px
      display inline-block
      margin :-8px 3px 0 0
    span
      font-size :1.6em
  .button-right
    float :right
  button:hover
    background :#2360DB
  .btn-delete
    width :34px;
    height :34px;
    background :#EDEDED;
    border :0px;
    margin :0 auto;
    &:hover
      background :#E0E0E0
  i
    margin :0  0 0 -12px
  .icon-delete
    color:#3E7AF6
    font-size:2.1em
</style>



