<template>
  <div class="strategy-table">
    <div class="ibox-content">
      <table>
        <thead>
        <tr>
          <th>策略名称</th>
          <th>使用映射</th>
          <th>备注</th>
          <th>工具</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in strategyList" :key="item.id">
          <td>{{item.strategname}}
            <i class="iconfont  icon-edit">
              &#xe6f4;</i></td>
          <td>{{item.mapping}}</td>
          <td>{{item.note}}</td>
          <td>
            <i class="iconfont  icon-plus-copy  strategy-tools">&#xe739;</i>
            <i class="iconfont icon-cloud-download  strategy-tools">&#xe632;</i>
            <i class="iconfont  icon-pushpin strategy-tools">&#xe63f;</i>
            <i class="iconfont icon-lock strategy-tools">&#xe649;</i>
          </td>
          <td class="ta">
            <a>查看规则 </a>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        strategyList: [
          {
            id: 1,
            strategname: 'CVE only',
            mapping: '所有镜像',
            note: 123

          },
          {
            id: 2,
            strategname: 'CVE only',
            mapping: '所有镜像',
            note: 123

          },
          {
            id: 3,
            strategname: 'CVE only',
            mapping: '所有镜像',
            note: 123
          }
        ]
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .strategy-table
    .ibox-content
      text-align: center
      vertical-align middle
      table
        width: 100%
        border-collapse: collapse
        margin-bottom: 40px
        thead
          tr
            background: #F7F7F7
            height: 50px
            box-shadow: 4px 4px 20px #FAFAFA
          th
            font-size: 1.5em
            font-weight: bold
            color: #525252
          th:first-child
            display inline-block
            margin 17px 0 0 -35px
        tbody
          tr
            font-size: 1.4em
            color: #525252
            height: 50px
            i
              display inline-block
              margin: 0 17px 0 0
            td:first-child
              color: #3E7AF6
              display inline-block
              margin 17px 0px 0 -35px
            td:last-child :hover
              color: #3E7AF6
              text-decoration: underline

  tr:hover
    background: #FAFAFA

  .strategy-tools
    color: #999999
    font-size: 1.3em

  .icon-edit
    margin: 0 0 0 10px
    font-size: 1.2em
</style>



